import React, { ReactElement, ReactNode, useEffect, useRef, useState } from "react";

interface KeepAliveProps {
    activeKey: string; // ÂΩìÂâçÊøÄÊ¥ªÁöÑÁªÑ‰ª∂ Key
    children: ReactElement; // ÈúÄË¶ÅÁºìÂ≠òÁöÑÁªÑ‰ª∂
}

// const cache = new Map<string, ReactNode>(); // ÁªÑ‰ª∂ÁºìÂ≠ò

const KeepAlive: React.FC<KeepAliveProps> = ({ activeKey, children }) => {
    const cacheRef = useRef(new Map<string, ReactNode>()); // ‰ΩøÁî® useRef Â≠òÂÇ®ÁªÑ‰ª∂ÁºìÂ≠ò
    const [, forceUpdate] = useState({}); // Áî®‰∫éËß¶ÂèëÁªÑ‰ª∂Êõ¥Êñ∞

    useEffect(() => {
        // Â¶ÇÊûúÁºìÂ≠ò‰∏≠Ê≤°ÊúâÂΩìÂâçÁªÑ‰ª∂ÔºåÂàôÂ≠òÂÖ•
        if (!cacheRef.current.has(activeKey)) {
            cacheRef.current.set(activeKey, children);
        }
        forceUpdate({}); // Ëß¶ÂèëÊõ¥Êñ∞
    }, [activeKey, children]);

    return (
        <div>
            {Array.from(cacheRef.current.entries()).map(([key, component]) => (
                <div
                    key={key}
                    style={{ display: key === activeKey ? "block" : "none" }} // ÊéßÂà∂ÊòæÁ§∫/ÈöêËóè
                >
                    {component}
                </div>
            ))}
        </div>
    );
};

export default KeepAlive;
